.swiper-template {
	--padding: 90px;
	padding: 0 var(--padding);
	margin: -15px calc(-30px - var(--padding)) 0;

	.swiper-template-nav {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		max-width: 1323px;
		width: 100%;
		display: flex;
		justify-content: space-between;
		z-index: 3;
	}

	.swiper-button-next,
	.swiper-button-prev {
		position: static;
		color: #6c6f7f;
		width: 22px;
	}
	&:before,
	&:after {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		display: inline-block;
		width: var(--padding);
		height: 100%;
		background: #282b38;

		z-index: 2;
	}
	&:after {
		left: calc(100% - var(--padding));
	}

	.swiper-slide {
		padding: 15px;
	}
}

.template-card {
	border-radius: 20px;
	background-color: $bg-secondary;
	padding: 0 20px 27px;
	overflow: hidden;
	text-align: center;
	box-shadow: 0px 0px 15px 0 rgb(6 9 27 / 45%);
	max-width: 360px;
	margin: 0 auto;
	height: 482px;
	.img-box {
		margin: 0 -20px 27px;
	}
}

.card-img {
	border-radius: 0;
	height: 229px;
	object-fit: contain;
}

.card-title {
	margin-bottom: 12px;
}

.card-options {
	margin-bottom: 18px;
}

.options {
	&-list {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 30px;
		margin-bottom: 8px;
	}
}

.option-item {
	img {
		margin-right: 10px;
		max-width: 24px;
	}
}

.card-price {
	color: #fff;

	span {
		font-size: 22px;
		line-height: 125%;

		&:first-child {
			color: #a3a3a3;
		}
	}

	strong {
		font-weight: 700;
		font-size: 45px;
		line-height: 1.3;
	}
}

.btn-row {
	display: flex;
	gap: 14px;

	.btn {
		width: 153px;
		justify-content: center;
		padding: 13px 8px;
	}
}
